<template>
    <div class="draw-tool map-tool">
        <div class="tool-btn"  @click="importTool('kml')">
            <el-button class="btn-import-kml" type="primary" title="导入kml"></el-button>
            <span>导入kml</span>
        </div>
        <div class="tool-btn" @click="importTool('kmz')">
            <el-button class="btn-import-kmz" type="primary" title="导入kmz"></el-button>
            <span>导入kmz</span>
        </div>
        <div class="tool-btn" @click="importTool('json')">
            <el-button class="btn-import-json" type="primary" title="导入json"></el-button>
            <span>导入json</span>
        </div>
        <div class="tool-btn" @click="importTool('shp')">
            <el-button class="btn-import-shp" type="primary" title="导入shp"></el-button>
            <span>导入shp</span>
        </div>
        <div class="tool-btn" @click="importTool('clear')">
            <el-button class="btn-clear" type="primary" title="清除导入"></el-button>
            <span>清除导入</span>
        </div>
        <div class="tool-btn">
             <div class="h-line"></div>
        </div>
        <div class="tool-btn" @click="selectFeatures">
            <el-button class="btn-select" type="primary" title="选择要素"></el-button>
            <span>选择要素</span>
        </div>
        <div class="tool-btn" @click="exportTool('kml')">
            <el-button class="btn-export-kml" type="primary" title="导出kml"></el-button>
            <span>导出kml</span>
        </div>
        <div class="tool-btn" @click="exportTool('kmz')">
            <el-button class="btn-export-kmz" type="primary" title="导出kmz"></el-button>
            <span>导出kmz</span>
        </div>
        <div class="tool-btn" @click="exportTool('json')">
            <el-button class="btn-export-json" type="primary" title="导出json"></el-button>
            <span>导出json</span>
        </div>
        <input type="file" id="fileInput" :accept="fileType" ref="inputEr" :multiple="true" @change="handleSuccess" style="display: none" />
    </div> 
</template>
<script>

import Select from 'ol/interaction/Select';
import {singleClick} from 'ol/events/condition'
import {exportJSON, exportKML} from "../mapJs/exportData";
import {Circle, Fill, Stroke, Style} from "ol/style";
import { clearSource, importJSON, importKml, importKmz, importSHP } from "../mapJs/importData"


let selectFeature = null;
let selectList = [];//记录要操作的要素 不重复
let selectListAll = [];//记录要操作的要素
export function initSpaceAnalysis() {
  clearSelect()
  selectFeature = new Select({
    condition: singleClick ,
    hitTolerance: 3,
    style: new Style({
      fill: new Fill({
        color: 'rgba(255,255,255,0.63)',
      }),
      stroke: new Stroke({
        color: '#20a3f9',
        width: 3
      }),
      image: new Circle({
        radius: 5, fill: new Fill({color: '#20a3f9'})
      }),
    })
  })
  window.olMap.addInteraction(selectFeature);
  selectFeature.on('select', selectFunction)
  return selectFeature
}

export function clearSelect() {
  if (selectFeature) {
    window.olMap.removeInteraction(selectFeature);
    selectFeature = null
    selectListAll.length = 0
  }
}

function selectFunction(event) {
  if (event.selected[0]) {
    selectListAll.push(event.selected[0])
  }
}

export const getAllSelectedFeatures = function(){
  let selectList = [...new Set(selectListAll)];
  return selectList;
}

/**
 * 导出kml 导出kmz  导出json
 */
export function exportData(param) {
  selectList = [...new Set(selectListAll)];
  let selects=[]
  selectList.forEach(f => {
    selects.push(f)
  })
  clearSelect()
  if (selects.length === 0) {
    return '0'
  }
  switch (param) {
    case 'kml':
      exportKML(selects, param)
      break;
    case 'kmz':
      exportKML(selects, param)
      break;
    case 'json':
      exportJSON(selects, param)
      break;
    case 'shp':
      exportJSON(selects, param)
      break;
  }
}

//导入工具按钮
const importTool = {
  'kml': { name: '导入kml', type: 'kml', suffix: '.kml,.ovkml'},
  'kmz': { name: '导入kmz', type: 'kmz', suffix: '.kmz,.ovkmz'},
  'json': { name: '导入json', type: 'json', suffix: '.json'},
  'shp': { name: '导入shp', type: 'shp', suffix: '.shp'},
}

export default ({
    data(){
        return {
          fileType: '',
          importType: '',
        }
    },
    methods: {
      importTool(type) {
        if (type === 'clear') {
          clearSource()
        } else {
          this.inputClick(importTool[type].suffix, type)
        }
      },
      inputClick(lx, pt) {
        this.importType = pt
        this.fileType = lx
        this.$nextTick(() => {
          this.$refs.inputEr.dispatchEvent(new MouseEvent('click'))
        })
      },
      //文件上传成功
      handleSuccess(e) {
        let files = e.target.files || e.dataTransfer.files;
        if (!files.length) return;
        for (let i = 0; i < files.length; i++) {
          //要做个判断 是不是kml文件 不是 返回
          let fileType = files[i].name.substring(files[i].name.lastIndexOf(".") + 1)
          let ms = ''
          switch (this.importType) {
            case 'kml':
              if (fileType !== "kml" && fileType !== "ovkml") {
                this.message('0', "上传文件只能是 ovkml 或 kml 格式!", 'msgError')
              } else {
                ms = importKml(files[i])
                console.log(ms)
                this.message(ms, "解析kml失败，请检查数据!", 'msgError')
              }
              break;
            case 'kmz':
              if (fileType !== "kmz" && fileType !== "ovkmz") {
                this.message('0', "上传文件只能是 ovkmz 或 kmz 格式!", 'msgError')
              } else {
                ms = importKmz(files[i])
                this.message(ms, "解析kmz失败，请检查数据!", 'msgError')
              }
              break;
            case 'json':
              if (fileType !== "json") {
                this.message('0', "上传文件只能是 json 格式!", 'msgError')
              } else {
                ms = importJSON(files[i])
                this.message(ms, "解析json失败，请检查数据!", 'msgError')
              }
              break;
            case 'shp':
              if (fileType !== "shp") {
                this.message('0', "上传文件只能是shp 格式!", 'msgError')
              } else {
                ms = importSHP(files[i])
                this.message(ms, "解析shp失败，请检查数据!", 'msgError')
              }
              break;
          }
        }
        e.target.value = ''
      },
      selectFeatures() {
        initSpaceAnalysis()
      },
      exportTool(param) {
        let dc = exportData(param)
        this.message(dc, '导出失败！请选择要素再导出', 'msgError')
      },
      message(num, msg, type) {
        // 0为警告 错误
        if (num === '0') {
          this.$modal[type](msg)
        }
      },
    }
})
</script>

<style lang="less" scoped>
.map-tool {
  .tool-btn{
    .btn-import-kml {
        background: url("../../../assets/icons/mapTool/import-kml.png") no-repeat;
        background-size: 100% 100%;
      }
      .btn-import-kmz {
        background: url("../../../assets/icons/mapTool/import-kmz.png") no-repeat;
        background-size: 100% 100%;
      }
      .btn-import-json {
        background: url("../../../assets/icons/mapTool/import-json.png") no-repeat;
        background-size: 100% 100%;
      }
      .btn-import-shp {
        background: url("../../../assets/icons/mapTool/import-shp.png") no-repeat;
        background-size: 100% 100%;
      }
      .btn-clear {
        background: url("../../../assets/icons/mapTool/clear.png") no-repeat;
        background-size: 100% 100%;
      }
      .btn-select {
        background: url("../../../assets/icons/mapTool/select.png") no-repeat;
        background-size: 100% 100%;
      }
      .btn-export-kml {
        background: url("../../../assets/icons/mapTool/export-kml.png") no-repeat;
        background-size: 100% 100%;
      }
      .btn-export-kmz {
        background: url("../../../assets/icons/mapTool/export-kmz.png") no-repeat;
        background-size: 100% 100%;
      }
      .btn-export-json {
        background: url("../../../assets/icons/mapTool/export-json.png") no-repeat;
        background-size: 100% 100%;
      }
  }
}
</style>