import GeoJSON from "ol/format/GeoJSON";
import Feature from 'ol/Feature.js';
import { Point, Polygon, Circle, LinearRing, LineString, MultiPoint, MultiPolygon, MultiLineString} from "ol/geom";
import WKT from "ol/format/WKT";
const format = new WKT()

// GeoJSON数据转化为feature
export function geojsonToFeatures(data){
    let geoJson= new GeoJSON({
        dataProjection: 'EPSG:4326',
        featureProjection: 'EPSG:4326'
    });
    let features;
    if (data.type === 'FeatureCollection') {
      features = geoJson.readFeatures(data);
    } else {
      features = geoJson.readFeature(data);
    }
    return features
  }

//创建要素
export function createFeature(data) {
    if (!data.geom) return;
    let feature = format.readFeature(data.geom, {
      dataProjection: 'EPSG:4326',
      featureProjection: 'EPSG:4326'
    });
    feature.setId(data.id);
    feature.set("data", data);
    return feature
}

// 创建要素
export function createFeatureByCooridate(type, data){
  if (!type || !data) return;
  let geometry = null;
  switch (type) {
    case "Point": 
      geometry = new Point(data.coordinates);
      break;
    case "Circle":
      geometry = new Circle(data.coordinates.center, data.coordinates.radius);
      break;
    case "Polygon": 
      geometry = new Polygon(data.coordinates);
      break;
    case "LinearRing":
      geometry = new LinearRing(data.coordinates);
      break;
    case "LineString":
      geometry = new LineString(data.coordinates);
      break;
    case "MultiPoint":
      geometry = new MultiPoint(data.coordinates);
      break;
    case "MultiPolygon": 
      geometry = new MultiPolygon(data.coordinates);
      break;
    case "MultiLineString": 
      geometry = new MultiLineString(data.coordinates);
      break;
  }
  if (geometry) {
    const feature = new Feature({
      geometry: geometry,
      name: data.name,
      data: data
    })
    return feature
  }
  
  
}