import { Message } from "element-ui";


// 数据保存到localStorage
export function setCache (key, value) {
  let saveString = '';
  if (Object.prototype.toString.call(value) === '[object Object]' || Object.prototype.toString.call(value) == "[object Array]") {
    saveString = JSON.stringify(value)
  } else {
    if (typeof value == "string" || typeof value == "number" || typeof value == "boolean") {
      saveString = value
    } else {
      Message.error({ message: Object.prototype.toString.call(value) })
    }
  }
  window.localStorage.setItem(key, saveString)
}

// 从缓存取数据
export function getCache (key, type) {
  let currentDataString = window.localStorage.getItem(key);
  let currentData = ''
  if (type == 'o' || type == "object" || type == "array" || type == "a") {
    currentData = JSON.parse(currentDataString)
  } else {
    currentData = currentDataString
  }
  return currentData;

}

// 判断非法字符
export function specialChar (val) {
  let strStatus = true;
  if (val.length > 0) {
    for (var chartIndex = 0; chartIndex < val.length; chartIndex++) {
      let currentCharValue = val.charAt(chartIndex).charCodeAt()
      // 判断不可见字符
      strStatus = strStatus && !(currentCharValue >= 0 && currentCharValue <= 31 || currentCharValue == 127)
    }

    let flag = new RegExp("[`~!@#$^&*()=|{}':;',\\[\\].<>《》/?~！@#￥……&*（）� ╇ ▎╁⊙→——|{}【】‘；：”“'。，、？ ]")
    if (flag.test(val)) {
      strStatus = false;
    }
    return strStatus
  }
}

// 验证字符串只包含数字和字母
export function onlyNumberString (string) {
  let checkString = string.trim();
  if (checkString == '') {
    Message.warning({ message: "请输入用户名" })
    return false
  } else {
    let re = /^[0-9a-zA-Z]*$/g;
    let result = re.test(checkString);
    if (!result) {
      Message.error({ message: "用户名只能是字母和数字的组合" })
    }
    return result
  }
}

// 获取x前的时间
export function getSomeTime (type, x, timeType, formatterType) {
  let nowDate = 0;
  let current_time = '';
  if (type != 5) {
    if (x) {
      let timeNumber = parseInt(x);
      if (timeType == 'hour') {
        current_time = new Date().getTime() + timeNumber * 1000 * 60 * 60;
      } else if (timeType == 'day') {
        current_time = new Date().getTime() + timeNumber * 1000 * 60 * 60 * 24;
      } else if (timeType == 'month') {
        current_time = new Date().getTime() + timeNumber * 1000 * 60 * 60 * 24;
      }
      nowDate = new Date(current_time);
    } else {
      nowDate = new Date();
    }
  } else {
    nowDate = new Date(x)
  }
  var Y = nowDate.getFullYear();
  var M = nowDate.getMonth() + 1;
  var d = nowDate.getDate();
  var h = nowDate.getHours();
  var m = nowDate.getMinutes();
  var s = nowDate.getSeconds();

  M = M >= 10 ? M : "0" + M;
  d = d >= 10 ? d : "0" + d;
  h = h >= 10 ? h : "0" + h;
  m = m >= 10 ? m : "0" + m;
  s = s >= 10 ? s : "0" + s;

  if (type == 1 || formatterType == 1) {
    return Y + "-" + M + "-" + d;
  } else if (type == 2) {
    return h + ":" + m + ":" + s;
  } else if (type == 3) {
    return Y - 1 + "-" + M + "-" + d + " " + h + ":" + m + ":" + s;
  } else if (type == 4) {
    return Y + "年" + M + "月" + d + "日 " + h + ":" + m + ":" + s;
  } else if (type == 5) {
    return Y + "-" + M + "-" + d + " " + h + ":" + m + ":" + s;
  } else {
    return Y + "-" + M + "-" + d + " " + h + ":" + m + ":" + s;
  }
}

// 判断数据的数值类型
export function checkNumberType (str, type) {
  let checkStatus = '';
  if (type == 1) {  //非负整数（正整数 + 0）
    checkStatus = /(^[1-9]\d*$)/.test(str)
  } else if (type == 2) { //最多2位小数
    checkStatus = /^(\d+)(\.\d{1,2})?$/.test(str)
  } else if (type == 3) { //浮点数
    checkStatus = /^ (-?\\d +) (\\.\\d +)?$/.test(str)
  }
  return checkStatus
}

// 设置cookie
export function setCookie (cname, cvalue, exdays) {
  if (exdays) {
    let d = new Date();
    let val = JSON.stringify(cvalue)
    d.setTime(d.getTime() + (exdays * 24 * 60 * 60 * 1000));
    let expires = "expires=" + d.toGMTString();
    document.cookie = cname + "=" + val + "; " + expires;
  } else {
    document.cookie = cname + "=" + cvalue + "; ";
  }
 
}

// 获取cookie
export function getCookie (cname) {
  let name = cname + "=";
  let ca = document.cookie.split(';');
  for (let i = 0; i < ca.length; i++) {
    let c = ca[i].trim();
    if (c.indexOf(name) == 0) return c.substring(name.length, c.length);
  }
  return "";
}