/*
 * @Description: KML json shp kmz 导出 操作 方法
 * @version:
 * @Author: lh
 * @Date: 2023-05-30
*/
import {Circle, Icon} from "ol/style";
import {KML} from "ol/format";
import {saveAs} from 'file-saver';
import jsZip from 'jszip'
import GeoJSON from "ol/format/GeoJSON";
// import {initFeatureStyle, setNewStyle} from "./setVectorStyle";


const shpWrite = require('shp-write');

/**
 * 判断是否为图片填充 要素 是的话 要重新赋样式 不然导出会报错
 * @param selectList 选择要素集合
 */
function isRenderPic(selectList) {
  let features = [];
  // Circle 不是标准图形，导出会报错
  selectList.forEach(f => {
    if (!(f.getGeometry() instanceof Circle)) {
      let type=f.getGeometry().getType()
      if (type === 'Point' || type === 'MultiPoint') {
        if (f.getStyle().getImage() instanceof Icon) {
          features.push(setStyle(f));
        } else {
          features.push(f);
        }
      } else if (type === 'LineString' || type === 'MultiLineString') {
        features.push(f);
      } else if (type === 'Polygon' || type === 'MultiPolygon') {
        if (typeof (f.getStyle().getFill().getColor()) === 'object') {
          features.push(setStyle(f));
        } else {
          features.push(f);
        }
      }
    }
  });
  return features
}
//给有图片的要素 赋值新的样式
function setStyle(f) {
  // let styleParam = initFeatureStyle()
  // f.setStyle(setNewStyle(f.getGeometry().getType(), styleParam))
  return f
}

/**
 * kml kmz导出
 * @param selectList 选择要素集合
 * @param type 导出文件类型
 * @param isWGS84
 */
export function exportKML(selectList, type) {
  let name = '要素导出'
  let features = isRenderPic(selectList)
  const kmlXML = new KML().writeFeaturesNode(features,{
    dataProjection: 'EPSG:4326',
    featureProjection: 'EPSG:4326'
  });
  // 最后使用saveAs导出成本地文件
  let str = new XMLSerializer().serializeToString(kmlXML)

  if (type === 'kml') {
    saveAs(new Blob([str], {
      type: 'text/plain;charset=utf-8'
    }), `${name}.kml`);
  } else {
    const blob = new Blob([str], {type: 'text/plain;charset=utf-8'})
    const zip = new jsZip()
    zip.file(`${name}.kml`, blob)
    zip.generateAsync({
      type: "blob"
    }).then(res => {
      saveAs(res, `${name}.kmz`)
    })
  }
}

/**
 * JSON shp导出
 * @param selectList 选择要素集合
 * @param type 导出文件类型
 * @param isWGS84
 */
export function exportJSON(selectList, type) {
  let name = '要素导出'
  let features = isRenderPic(selectList)
  const json = new GeoJSON().writeFeaturesObject(features,{
    dataProjection: "EPSG:4326", featureProjection: "EPSG:4326"
  });
  const content = JSON.stringify(json);
  if (type === 'json') {
    saveAs(new Blob([content], {
      type: 'text/plain;charset=utf-8'
    }), `${name}.json`);
  } else {
    // 定义 依赖库 shp-write 的下载配置/
    let options = {
      file: 'myShapeFiles', folder: 'myShapes',//文件夹名字
      types: {
        point: 'mypoints', polygon: 'mypolygons', polyline: 'mylines'   //polyline  line  LineString
      }
    }
    shpWrite.download(json, options) //下载数据
  }
}


