import { Message } from "element-ui";
import axios from "axios"
// import '../../public/config.js'
import { getCookie } from "../utils/common.js";

let baseUrl = ''

let httpService = axios.create({
  baseUrl: baseUrl,
  timeout: 60000
})

httpService.interceptors.request.use(config => {
  let tokenString = getCookie("token")
  let token = tokenString ? decodeURIComponent(tokenString) : null
  // 请求成功处理
  if (token) {//判断浏览器中的cookie中是否存在项目的token
    config.headers.token = token
    config.headers.Authorization = 'Bearer ' + token
  }
  return config;
}, err => {
  Promise.reject(err);// 请求错误处理
})


httpService.interceptors.response.use(response => {
  // TODO:具体的code对应的处理可继续添加修改
  if (response.data.code === 200) {
    console.log('请求成功')
  }
  if (response.data.code === 300) {
    console.log(response.data.msg)
  }
  return response;
}, err => {
  // TODO:具体的code对应的处理可继续添加修改
  if (err.response.code === 301 || err.response.code === 401) {
    console.log('登录过期');
    //登录过期跳转登录页面，并将要浏览的页面fullPath传过去，登录成功后跳转需要访问的页面 ---主页(index) 或者 退出到登录前的浏览的页面  
    //这样登录页面登录接口成功之后可以进行跳转 主页(index) 或者 退出到登录前的页面： let path = this.$route.query.redirect || '/index.js';   this.$router.push(path);
    setTimeout(() => {
      this.$router.replace({
        path: '/Login',
        query: {
          redirect: this.$router.currentRoute.fullPath
        }
      });
    }, 1000);
    document.cookie = "token=;";//清除token
  }
  if (err.response.code === 500) {
    Message({
      message: "网络故障，请联系系统管理员",
      type: 'error'
    })
  }
  return Promise.reject(err);
})


export function get (url, params = {}, headers = {}) {
  return new Promise((resolve, reject) => {

    httpService({
      url: url.indexOf('.json') > -1 ? url : baseUrl + url,
      method: 'get',
      params: params,
      headers: headers
    }).then(res => {
      resolve(res);
    }).catch(err => {
      reject(err);
      Message({
        message: "网络故障，请联系系统管理员",
        type: 'error'
      })
    })
  })
}


export function post (url, params = {}, headers = {}) {
  return new Promise((resolve, reject) => {
    httpService({
      url: baseUrl + url,
      method: 'post',
      data: params,
      headers: headers
    }).then(res => {

      resolve(res);
    }).catch(err => {
      reject(err);
      Message({
        message: "网络故障，请联系系统管理员",
        type: 'error'
      })
    })
  })
}

export default {
  get,
  post
}
