<template>
    <div class="draw-tool map-tool">
        <div class="tool-btn"  @click="addBaseDraw('Point')">
            <el-button class="btn-point" type="primary" title="绘制点"></el-button>
            <span>绘制点</span>
        </div>
        <div class="tool-btn" @click="addBaseDraw('LineString')">
            <el-button class="btn-line" type="primary" title="绘制线"></el-button>
            <span>绘制线</span>
        </div>
        <div class="tool-btn" @click="addBaseDraw('Polygon')">
            <el-button class="btn-polygon" type="primary" title="绘制面"></el-button>
            <span>绘制面</span>
        </div>
        <div class="tool-btn" @click="addBaseDraw('Circle')">
            <el-button class="btn-circle" type="primary" title="绘制圆"></el-button>
            <span>绘制圆</span>
        </div>
        <div class="tool-btn" @click="addBaseDraw('Rectangle')">
            <el-button class="btn-rectangle" type="primary" title="绘制矩形"></el-button>
            <span>绘矩形</span>
        </div>
        <div class="tool-btn">
            <div class="h-line"></div>
        </div>
        <div class="tool-btn" @click="closeInteraction()">
            <el-button class="btn-cancel" type="primary" title="取消绘制"></el-button>
            <span>取消绘制</span>
        </div>
        <drawInfoDialogVue ref="drawer"></drawInfoDialogVue>
    </div>
</template>
<script>
import { Draw } from 'ol/interaction'
import { createBox, createRegularPolygon } from 'ol/interaction/Draw'

import { drawLayer } from "@/mapConfig/mapVectorLayers.js"
import { getDrawData } from "@/httpService/api.js"
import { createFeatureByCooridate } from "@/views/openlayer-tutorial/mapJs/loadGeoJsonData.js"

import drawInfoDialogVue from './drawInfoDialog.vue'

export default {
    components: { drawInfoDialogVue },
    data(){
        return {
            map: null,
            drawLayer: null,
            drawSurce: null,
            baseDraw: null,
            baseModify: null,
            currentDrawFeature: null,
            currentFeatureGeom: null,
        }
    },
    mounted(){
        let that = this;
        that.$nextTick(() => {
            that.map = window.olMap;
            that.drawLayer = drawLayer;
            that.drawSource = that.drawLayer.getSource();
            // that.baseModify = new Modify();
            that.map.on("click", that.handleClickMap)
        })
        // that.getDrawList()
        
    },
    methods: {
        addBaseDraw(imageType) {
            let that = this;
            that.closeInteraction()
            let drawOptions = {
                source: that.drawSource,
                type: imageType
            };
            if (imageType == 'Rectangle') {
                drawOptions.type = 'Circle'
                drawOptions.geometryFunction = createBox()
            } 
            if (imageType === 'Circle') {
                drawOptions.geometryFunction = createRegularPolygon()
            }
            that.baseDraw = new Draw(drawOptions);
            that.baseDraw.on('drawend', (evt) => {
                let featureGeom = evt.feature.getGeometry();
                that.currentDrawFeature = evt.feature;
                that.currentDrawFeature.set("layer_name", "drawTools")
                that.currentFeatureGeom = featureGeom.getCoordinates();
                console.log(that.currentFeatureGeom)
                if (that.baseDraw) {
                    that.map.removeInteraction(that.baseDraw)
                    that.baseDraw = null; 
                    that.$refs.drawer.initDrawer('add', that.currentDrawFeature)
                }
            })
            that.map.addInteraction(that.baseDraw);
        },
        closeInteraction(){
            let that = this;
            if (that.baseDraw) {
                that.map.removeInteraction(that.baseDraw)
                that.baseDraw = null;
            }
        },
        handleClickMap (event){
            let that = this;
            let feature = that.map.forEachFeatureAtPixel(event.pixel, function (feature) {
                return feature;
            });
            if (feature && feature.values_ && feature.values_.layer_name === 'drawTools') {
                that.$refs.drawer.initDrawer('edit', feature)
            }     
        },
        getDrawList(){
            let that = this;
            getDrawData().then(res => {
                if (res && res.status === 200 && res.data) {
                    let drawList = res.data;
                    drawList.map(item => {
                        let feature = createFeatureByCooridate(item.type, item)
                        feature.set('layer_name', 'drawTools')
                        that.drawSource.addFeature(feature)
                    })
                }
                console.log(res)
            })
        }
    }
}
</script>

<style scoped>
.btn-point {
    background: url("../../../assets/icons/mapTool/point.png") no-repeat;
    background-size: 100% 100%;
}

.btn-line {
    background: url("../../../assets/icons/mapTool/line.png") no-repeat;
    background-size: 100% 100%;
}

.btn-polygon {
    background: url("../../../assets/icons/mapTool/polygon.png") no-repeat;
    background-size: 100% 100%;
}

.btn-circle {
    background: url("../../../assets/icons/mapTool/circle.png") no-repeat;
    background-size: 100% 100%;
}

.btn-rectangle {
    background: url("../../../assets/icons/mapTool/rectangle.png") no-repeat;
    background-size: 100% 100%;
}

.btn-cancel {
    background: url("../../../assets/icons/mapTool/cancelDraw.png") no-repeat;
    background-size: 100% 100%;
}
</style>