<template>
    <el-drawer title="我是标题" :visible.sync="drawerVisible" :wrapperClosable="false" append-to-body :modal="false" :show-close="true" style="width: 20vw;margin-left: 80vw;" size="100%">
        <el-form :model="featureForm" ref="featureInfoForm" label-width="80px" class="demo-ruleForm" size="mini">
            <el-tabs type="border-card">
                <el-tab-pane label="基本属性">
                    <el-form-item label="标注文字" prop="text" :rules="[ { required: true, message: '请输入标注文字'}]">
                        <el-input  v-model="featureForm.text" ></el-input>
                    </el-form-item>
                    <el-form-item label="标注显示" prop="showText">
                        <el-switch v-model="featureForm.showText"></el-switch>
                    </el-form-item>
                    <el-form-item label="字体大小" prop="fontSize">
                        <el-input-number :min="10" v-model="featureForm.fontSize"></el-input-number>
                    </el-form-item>
                    <el-form-item label="文字颜色" prop="color">
                        <el-color-picker v-model="featureForm.color"></el-color-picker>
                    </el-form-item>
                    <el-form-item label="图片显示" prop="showImage" v-if="imageType === 'Point'">
                        <el-switch v-model="featureForm.showImage"></el-switch>
                    </el-form-item>
                    <el-form-item label="图标比列" prop="scale"  v-if="imageType === 'Point'">
                    <el-input-number :min="0.1" :max="2" :step="0.1" v-model="featureForm.scale"></el-input-number>
                </el-form-item>
                <el-form-item label="图标" prop="image"  v-if="imageType === 'Point'">
                    <img class="pointImg" :src="featureForm.image" @click="editIconModal('point')">
                </el-form-item>
                <el-form-item label="线条宽度" prop="lineWidth"  v-if="imageType !== 'Point'">
                    <el-input style="width: 150px" placeholder="请输入内容" v-model="featureForm.lineWidth">
                        <template slot="append">像素</template>
                    </el-input>
                </el-form-item>
                <el-form-item label="线型" prop="lineDash"  v-if="imageType === 'LineString'">
                    <el-select  style="width: 150px" v-model="featureForm.lineDash" placeholder="请选择">
                        <el-option v-for="item in lineType" :key="item.value" :label="item.label" :value="item.value"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="线条颜色" prop="lineColor"  v-if="imageType !== 'Point'">
                    <el-color-picker style="width: 150px"  v-model="featureForm.lineColor"></el-color-picker>
                </el-form-item>
                <el-form-item label="填充类型" prop="fillType"  v-if="imageType !== 'LineString' && imageType !== 'Point'">
                    <el-select  style="width: 150px" v-model="featureForm.fillType" placeholder="请选择">
                        <el-option v-for="item in areaFillTypeArr" :key="item.value" :label="item.label" :value="item.value"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="填充预览" prop="fillColor"  v-if="imageType !== 'LineString' && imageType !== 'Point'">
                    <el-color-picker style="width: 150px" v-if="featureForm.fillType === 'color'" v-model="featureForm.fillColor" show-alpha></el-color-picker>
                    <img v-if="featureForm.fillType === 'image'" :src="featureForm.image">
                </el-form-item>
                
                </el-tab-pane>
                <el-tab-pane label="要素信息">
                    <el-form-item label="要素名称" prop="powerName" :rules="[ { required: true, message: '请输入要素名称'}]">
                        <el-input style="width: 150px" v-model="featureForm.powerName" ></el-input>
                    </el-form-item>
                </el-tab-pane>
            </el-tabs>
        </el-form>
        <div class="demo-drawer__footer" style="margin-top: 20px">
            <el-button @click="drawerVisible = false" size="mini">取 消</el-button>
            <el-button type="primary" size="mini" :disabled="loading" v-loading="loading" @click="saveFeatureInfo">保存</el-button>
        </div>
    </el-drawer>
</template>
<script>

import { markImage, getFeatureStyle } from "../mapJs/setVectorStyle"

export default ({
    data() {
        return {
            drawerVisible: false,
            loading: false,
            handleType: '',
            handleFeature: '',
            imageType: '',
            featureForm: {
                text: "",
                showText: true,
                fontSize: 14,
                color: "#666",
                showImage: true,
                scale: 1,
                image: markImage[0].url,
                lineWidth: 2,
                lineDash: '[0,0]',
                lineColor: 'rgba(90,140,204,1)',
                fillType: 'color',
                fillColor: "rgba(90,140,204,0.25)"
            },
            lineType: [
                {label: '——————————', value: '[0,0]'},
                {label: '-----------------------------',value: '[5, 5, 5, 5]'},
                {label: '—— —— —— —— —',value: '[40, 5, 40, 5]'},
                {label: '—— - —— - —— - —',value: '[40, 5, 5, 5]'},
            ],
            areaFillTypeArr: [
                {label: '固定颜色', value: 'color'},
                {label: '图片填充',value: 'image'},
            ],
        }
    },
    mounted(){

    },
    methods: {
        initDrawer(type, feature){
            let that = this;
            that.drawerVisible = true;
            that.handleType = type;
            that.imageType = feature ?  feature.getGeometry().getType() : '';
            that.handleFeature = feature;
            if (type === 'add') {
                console.log(that.imageType)
            } else if (type === "edit") {
                let info = that.handleFeature.values_ && that.handleFeature.values_.info ? that.handleFeature.values_.info : {};
                Object.keys(info).forEach(k => {
                    that.$set(that.featureForm, k, info[k])
                })
            }
        },
        cancelDrawer(){
            let that = this;
            that.drawerVisible = false;
            that.$refs.featureInfoForm.resetFields()
        },
        saveFeatureInfo() {
            let that = this;
            let info = JSON.parse(JSON.stringify(that.featureForm))
            that.loading = true;
            that.handleFeature.set("info", info)
            let style = getFeatureStyle(that.imageType, that.featureForm, that.handleFeature)
            that.handleFeature.setStyle(style)
            console.log( that.handleFeature)
            let param = {
                name: that.featureForm.text,
                coordinates: that.handleFeature.getGeometry().getCoordinates(),
                type: that.handleFeature.getGeometry().getType(),
                style: info
            }
            console.log(param)
            setTimeout(()=> {
                that.loading = false;
                that.cancelDrawer()
            }, 3000)
        }
    }
})
</script>