<template>
    <div ref="olMap" class="ol-map" id="ol-map">
        <mapTools ref="region" :map="map"></mapTools>
        
        
    </div>
</template>
<script>


import { controlConfig, initMap, changeUnderlayer } from "@/mapConfig/mapLoading"



import mapTools from "./mapTools/index.vue"

export default ({
    components: {mapTools},
    data() {
        return {
            map: null,
            viewConfig: {
                center: [104.88925, 34.02372],
                projection: 'EPSG:4326', // 坐标类型  'EPSG:4326' 经纬度 'EPSG:3857' 投影坐标
                enableRotation: false, //地图是否可以旋转
                // extent:[68,16,153,54], //限制地图可视范围 [ minX, minY, maxX, maxY]
                // smoothExtentConstraint: true,  //如果为true，范围约束将被平滑地应用，即允许视图稍微超出给定的extent。
                zoom: 4,  // 仅在resolution未定义时使用。缩放级别用于计算视图的初始分辨率。
                // maxZoom: 4,  // 用于确定分辨率约束的最大缩放级别。
                // minZoom: 20,  // 用于确定分辨率约束的最小缩放级别。
                rotation: 0,  // 以弧度为单位的视图初始旋转（顺时针旋转，0 表示北）
            },
            controlConfig: controlConfig,
            
        }
    },
    mounted () {
        let that = this;
        that.$nextTick(() => {
            let target = that.$refs.olMap;
            that.map =  initMap(target, that.viewConfig, that.controlConfig, 'ARCGIS_YXT');
        })
    },
    methods: {
        changeUnderlayer(){
            changeUnderlayer('GD_DXT')
        },
        setCenterAnimate(){
            let that = this;
            let view = that.map.getView()
            view.animate({zoom: 5}, {center: that.viewConfig.center}, {duration: 100000});
        },
        
    }
})
</script>
<style scoped>
.ol-map{
    width: 100%;
    height: 100%;
    background: blue;
    position: relative;
}
</style>
