import { importLayer } from "@/mapConfig/mapVectorLayers.js"

import {KML} from "ol/format";
import JSZip from "jszip";
import GeoJSON from "ol/format/GeoJSON";

let importSource = importLayer.getSource();

const shapefile = require("shapefile")

/**
 * kml导入
 * @param file 上传的文件
 */
export function importKml(file) {
  // 创建 KML 解析器实例
  let kmlFormat = new KML();
  return readFile(file, kmlFormat)
}

/**
 * kmz导入
 * @param file 上传的文件
 */
export function importKmz(file) {
  return new Promise(() => {
    const reader = new FileReader();
    let kmlFormat = new KML();
    reader.onload = () => {
      const zip = new JSZip();
      zip.loadAsync(reader.result).then((contents) => {
        const kmlFile = contents.file(/.*\.kml/)[0];
        kmlFile.async("string").then((kmlContent) => {
          renderFeature(kmlFormat, kmlContent)
        });
      }).catch((error) => {
        console.log(error)
        return '0'
      });
    };
    reader.readAsArrayBuffer(file);
  });
}

/**
 * json导入
 * @param file 上传的文件
 */
export function importJSON(file) {
  let geoJSON = new GeoJSON();
  return readFile(file, geoJSON)
}

/**
 * shp导入
 * @param file 上传的文件
 */
export function importSHP(file) {
  const reader = new FileReader()
  reader.readAsArrayBuffer(file)
  reader.onload = function() {
    shapefile.open(reader.result)
      .then((source) =>
        source.read().then(function log(result) {
          if (result.done) return
          if (result.value != null) {
            const value = result.value
            let geoJSON = new GeoJSON();
            renderFeature(geoJSON,JSON.stringify(value))
          }
          return source.read().then(log)
        })
      )
      .catch((error) => {
        console.log(error)
        return '0'
      })
  }
  return '1'
}

/**
 * 读取文件公共方法
 * @param file obj - 上传的文件
 * @param data obj - 实例类型 json或者kml等
 */
function readFile(file, data) {
  let reader = new FileReader();
  reader.onload = function () {
    let text = reader.result;
    return renderFeature(data, text)
  };
  reader.readAsText(file);
  return '1'
}

/**
 * 渲染feature
 * @param text obj - 读取文件转换后的文本数据
 * @param data obj - 实例类型 json或者kml等
 */
function renderFeature(data, text) {
  let features = data.readFeatures(text, {
    dataProjection: "EPSG:4326",
    featureProjection: "EPSG:4326"
  });
  if(features.length===0){
    return '0'
  }
  importSource.addFeatures(features)
  // 将地图视图调整为适应 KML 数据范围
  let extent = importSource.getExtent();
  window.olMap.getView().fit(extent);
}

export function clearSource(){
  importSource.clear()
}
