const createUnderlayer  = function() {
  let underlayers = [];
  Object.keys(baseConfig).forEach(k => {
    let layerConfig = baseConfig[k];
    if (layerConfig.url instanceof Array) {
      let underlayerConfig = {...layerConfig};
      let underlayerMarkConfig = {...layerConfig};
      underlayerConfig.url = layerConfig.url[0]
      underlayerMarkConfig.url = layerConfig.url[1]
      let underlayer = new TileLayer({
          title: underlayerConfig.title,
          source: new XYZ(underlayerConfig),
          visible: underlayerConfig.visible,
          //extent: [],
          zIndex: 5,
          properties: {id: k},
      })
      underlayers.push(underlayer)
      let underlayerMark = new TileLayer({
        title: underlayerMarkConfig.title,
        source: new XYZ(underlayerMarkConfig),
        visible: underlayerMarkConfig.visible,
        //extent: [],
        zIndex: 6,
        properties: {id: k},
      })
      underlayers.push(underlayerMark)
    } else {
      if (layerConfig.type === "BING") {
        let source = new BingMaps({
          crossOrigin: 'anonymous',
          culture: 'zh-CN',
          key: layerConfig.url,
          imagerySet: layerConfig.layer
        });
        let layer = new TileLayer({
            title: layerConfig.title,
            source: source,
            visible: layerConfig.visible,
            //extent: [],
            zIndex: 5,
            properties: {id: k},
        })
        underlayers.push(layer)
      } else {
        let layer = new TileLayer({
            title: layerConfig.title,
            source: new XYZ(layerConfig),
            visible: layerConfig.visible,
            //extent: [],
            zIndex: 5,
            properties: {id: k},
        })
        underlayers.push(layer)
      }
      
    }

  })
  return underlayers
}
const underlayersArr = createUnderlayer()



