export function initFeatureStyle() {
  return {
    //图片样式
    imgName: 'point',//图片名字
    imgScale: 1,//图片比例
    //圆点样式
    pointColor: '#D02090', pointRadius: 5,

    isImage: '0',//是否图片 1 是 0 否
    isShowLabel: '0', //标注
    text: '',//显示的文本
    textScale: 15, textColor: '#EE3B3B', //线样式
    lineWidth: 3,//线条宽度
    lineColor: '#4e89d4',//线条颜色 0透明 16进制数据
    lineDash: [],//线型
    //面样式
    polygonColor: '#EE3B3B',//面线条颜色
    polygonWidth: 2,//面线条宽度
    polygonFillColor: 'rgba(205,133,63,0.5)'//面填充颜色
  }
}

