// 用图片填充区域
export function rendererImage(src, color, breadth, feature) {
    let stroke = new Stroke({
      color: color, 
      width: breadth,
    });
    let cnv = document.createElement('canvas');
    let ctx = cnv.getContext('2d');
    ctx.setTransform(1,0.5, -0.5, 1, 30, 10); //a	水平扩展 b	水平倾斜 c	垂直倾斜 d	垂直扩展 e	水平移动 f	垂直移动
    ctx.fillStyle = "#00ff51"; //设置图片颜色
    let img = new Image();
    img.crossOrigin = 'anonymous';
    img.src = src;
    img.onload = function () {
      let pattern = ctx.createPattern(img, 'repeat');
      let fill = new Fill({
        color: pattern
      });
      let style = new Style({
        fill: fill,
        stroke: stroke
      });
      feature.setStyle(style)
    }
}


//根据styleParam配置项返回要素样式
export function getFeatureStyle(geoType, styleParam, feature) {
    let style = null
    let pointImage = null;
    let text = new Text({
      font: styleParam.fontSize + 'px Microsoft YaHei',
      //对齐方式
      textAlign: 'center',
      //文本基线
      textBaseline: 'middle',
      offsetY: 12,
      text: styleParam.showText ? styleParam.text : '',
      fill: new Fill({
        color: styleParam.color || '#222'
      }),
      stroke: new Stroke({
        // 文字描边
        color: styleParam.textStrokeColor || '#fff',
        width: 2
      })
    })
    switch (geoType) {
      case 'Point':
        if (styleParam.showImage && styleParam.image) {
          pointImage = new Icon({
            anchor: [0.5, 0.95],
            anchorOrigin: 'top-left',
            offsetOrigin: 'bottom-center',
            scale: styleParam.scale,
            opacity: 1,  
            src: styleParam.image
          });
        } else {
          pointImage = new CircleStyle({
            radius: 4,
            fill: new Fill({
                color: "rgba(90,140,204,1)"
            })
          })
        }
        style = new Style({
          image: pointImage,
          text: text
        })
        break
      case 'LineString':
        style = new Style({
          stroke: new Stroke({
            color: styleParam.lineColor,//线条颜色
            width: styleParam.lineWidth,//线条宽度
            lineDash: JSON.parse(styleParam.lineDash) //线型
          }),
          text: text
        })
        break
      case 'MultiLineString':
        style = new Style({
          stroke: new Stroke({
            color: styleParam.lineColor,//线条颜色
            width: styleParam.lineWidth,//线条宽度
            lineDash: styleParam.lineDash//线型
          }),
          text: text
        })
        break
      case 'Polygon':
        style = setPlStyle(styleParam, text, feature)
        break
      case 'Circle':
        style = setPlStyle(styleParam, text, feature)
        break
      case 'Rectangle':
        style = setPlStyle(styleParam, text, feature)
        break
    }
    return style
  }
  
  //设置面类型的要素样式
  function setPlStyle(styleParam, text, feature) {
    if (styleParam.fillType === 'image') {
      rendererImage(styleParam.image, styleParam.lineColor, styleParam.lineWidth, feature)
      return null;
    } else {
      let style = new Style({
        fill: new Fill({
          color: styleParam.fillColor
        }),
        stroke: new Stroke({
          color: styleParam.lineColor ? styleParam.lineColor : '#4e89d4',
          width: styleParam.lineWidth ? styleParam.lineWidth : 2
        }),
        text: text
      })
      return style
    }

  }