import {
  Circle as CircleStyle,
  Fill,
  RegularShape,
  Stroke,
  Style,
  Text
} from 'ol/style'
import { Draw, Modify } from 'ol/interaction'
import { LineString, Point } from 'ol/geom'
import { getArea, getLength } from 'ol/sphere'
import { measureLayer } from "@/mapConfig/mapVectorLayers.js"

let draw; 
let tipPoint; 
let map_ = null;
const source = measureLayer.getSource();

export const measureDefaultStyle = new Style({
    fill: new Fill({
      color: 'rgba(0, 153, 255, 0.2)',
    }),
    stroke: new Stroke({
      color: 'rgba(0, 153, 255, 1)',
      lineDash: [10, 10],
      width: 3,
    }),
    image: new CircleStyle({
      radius: 5,
      stroke: new Stroke({
        color: 'rgba(0, 153, 255, 1)',
      }),
      fill: new Fill({
        color: 'rgba(0, 153, 255, 1)',
      }),
    }),
  });
  
  export  const labelStyle = new Style({
    text: new Text({
      font: '14px Calibri,sans-serif',
      fill: new Fill({
        color: 'rgba(255, 255, 255, 1)',
      }),
      backgroundFill: new Fill({
        color: 'rgba(0, 153, 255, 1)',
      }),
      padding: [3, 3, 3, 3],
      textBaseline: 'bottom',
      offsetY: -15,
    }),
    image: new RegularShape({
      radius: 8,
      points: 3,
      angle: Math.PI,
      displacement: [0, 10],
      fill: new Fill({
        color: 'rgba(0, 153, 255, 1)',
      }),
    }),
  });
  
  export const tipStyle = new Style({
    text: new Text({
      font: '12px Calibri,sans-serif',
      fill: new Fill({
        color: 'rgba(255, 255, 255, 1)',
      }),
      backgroundFill: new Fill({
        color: 'rgba(0, 153, 255, 1)',
      }),
      padding: [2, 2, 2, 2],
      textAlign: 'left',
      offsetX: 15,
    }),
  });
  
  export const modifyMeasureStyle = new Style({
    image: new CircleStyle({
      radius: 5,
      stroke: new Stroke({
        color: 'rgba(0, 153, 255, 1))',
      }),
      fill: new Fill({
        color: 'rgba(0, 153, 255, 1)',
      }),
    }),
    text: new Text({
      text: '可拖动编辑节点',
      font: '12px Calibri,sans-serif',
      fill: new Fill({
        color: 'rgba(255, 255, 255, 1)',
      }),
      backgroundFill: new Fill({
        color: 'rgba(0, 153, 255, 1)',
      }),
      padding: [2, 2, 2, 2],
      textAlign: 'left',
      offsetX: 15,
    }),
  });


  const modify = new Modify({ source: source, style: modifyMeasureStyle })

  
  export const segmentStyle = new Style({
    text: new Text({
      font: '12px Calibri,sans-serif',
      fill: new Fill({
        color: 'rgba(255, 255, 255, 1)',
      }),
      backgroundFill: new Fill({
        color: 'rgba(0, 153, 255, 1)',
      }),
      padding: [2, 2, 2, 2],
      textBaseline: 'bottom',
      offsetY: -12,
    }),
    image: new RegularShape({
      radius: 6,
      points: 3,
      angle: Math.PI,
      displacement: [0, 8],
      fill: new Fill({
        color: 'rgba(0, 153, 255, 1)',
      }),
    }),
  });
  
 export const segmentStyles = [segmentStyle];
  
  export const formatLength = function (line) {
    const length = getLength(line, {
        projection: "EPSG:4326",
    });
    let output;
    if (length > 100) {
      output = Math.round((length / 1000) * 100) / 100 + ' km';
    } else {
      output = Math.round(length * 100) / 100 + ' m';
    }
    return output;
  };
  
  export const formatArea = function (polygon) {
    const area = getArea(polygon, {
        projection: "EPSG:4326",
    });
    let output;
    if (area > 10000) {
      output = Math.round((area / 1000000) * 100) / 100 + ' km\xB2';
    } else {
      output = Math.round(area * 100) / 100 + ' m\xB2';
    }
    return output;
  };


  export const styleFunction = function (feature, segments, drawType, tip) {
    let that = this;
    const styles = [];
    const geometry = feature.getGeometry();
    const type = geometry.getType();
    let point, label, line;
    if (!drawType || drawType === type || type === 'Point') {
        styles.push(measureDefaultStyle);
        if (type === 'Polygon') {
            point = geometry.getInteriorPoint();
            label = formatArea(geometry);
            line = new LineString(geometry.getCoordinates()[0]);
        } else if (type === 'LineString') {
            point = new Point(geometry.getLastCoordinate());
            label = formatLength(geometry);
            line = geometry;
        }
    }
    if (segments && line) {
        let count = 0;
        line.forEachSegment(function (a, b) {
            const segment = new LineString([a, b]);
            const label = formatLength(segment);
            if (segmentStyles.length - 1 < count) {
                segmentStyles.push(segmentStyle.clone());
            }
            const segmentPoint = new Point(segment.getCoordinateAt(0.5));
            segmentStyles[count].setGeometry(segmentPoint);
            segmentStyles[count].getText().setText(label);
            styles.push(segmentStyles[count]);
            count++;
        });
    }
    if (label) {
        labelStyle.setGeometry(point);
        labelStyle.getText().setText(label);
        styles.push(labelStyle);
    }
    if ( tip && type === 'Point' && !that.measureModify.getOverlay().getSource().getFeatures().length ) {
        that.tipPoint = geometry;
        tipStyle.getText().setText(tip);
        styles.push(tipStyle);
    }
    return styles;
}



// 测量交互
function addInteraction(isSegment, drawType, isClear) {
  const activeTip = '点击绘制 ' + (drawType === 'Polygon' ? '面' : '线')
  const idleTip = '点击开始测量'
  let tip = idleTip
  draw = new Draw({
    source: source,
    type: drawType,
    style: function(feature) {
      return styleFunction(feature, isSegment, drawType, tip)
    }
  })
  draw.on('drawstart', function() {
    if (isClear) {
      source.clear()
    }
    modify.setActive(false)
    tip = activeTip
  })
  draw.on('drawend', function() {
    modifyMeasureStyle.setGeometry(tipPoint)
    modify.setActive(true)
    map_.once('pointermove', function() {
      modifyMeasureStyle.setGeometry()
    })
    tip = null
    removeInteraction()
  })
  modify.setActive(false)
  map_.addInteraction(draw)
}
// 移除测量交互
function removeInteraction() {
  if (draw != null || undefined) {
    map_.removeInteraction(draw)
  }
}

// 测量图层
export function initMeasureLayer(map) {
  map_ = map
  map.addInteraction(modify)
}
// 测量函数
// @param  isSegment   是否展示每一段长  true展示  false 不展示
// @param  isClear  是否清除之前结果  true清除  false不清除
// @param drawType  测量类型  线  LineString  或者面 Polygon
export function measureHandle(isSegment, drawType, isClear) {
  measureLayer.setStyle(function(feature) {
    return styleFunction(feature, isSegment)
  })
  removeInteraction()
  addInteraction(isSegment, drawType, isClear)
}
// 清除测量结果
export function measureClear() {
  source.clear()
  removeInteraction()
}

