<template>
    <div class="measure-tool map-tool">
        <div class="tool-btn"  @click="addMeasure('distance')">
            <el-button class="btn-distance" type="primary" title="测距"></el-button>
            <span>测距</span>
        </div>
        <div class="tool-btn"  @click="addMeasure('area')">
            <el-button class="btn-area" type="primary" title="测面积"></el-button>
            <span>测面积</span>
        </div>
        <div class="tool-btn" >
            <el-button class="btn-clear" type="primary" title="清除临时数据(测量和定位)" @click="clearMeasureMark('clear')"></el-button>
            <span>清除</span>
        </div>
    </div>
</template>
<script>
import { measureLayer } from "@/mapConfig/mapVectorLayers.js"
import { Draw, Modify } from 'ol/interaction'
import {LineString, Point} from 'ol/geom.js';
import {
  modifyMeasureStyle, 
  measureDefaultStyle, 
  tipStyle, 
  segmentStyles, 
  labelStyle, 
  segmentStyle, 
  formatArea, 
  formatLength
} from "../mapJs/measure"

export default ({
    props: ['mapObject'],
    data() {
        return {
            measureDraw: null,  // 测量绘制对象
            measureVector: null, // 测量图层
            measureSource: null, // 测量图层数据源
            measureModify: null, // 测量编辑
            map: null,
            tipPoint: null,
        }
    },
    methods: {
        initMeasureLayer() {
            let that = this;
            that.measureVector = measureLayer
            that.measureVector.setStyle(function(f) {
                return that.styleFunction(f, true)
            })
            that.measureSource = that.measureVector.getSource(); //图层数据源
            that.measureModify = new Modify({source: that.measureSource, style: modifyMeasureStyle})
            that.map.addInteraction(that.measureModify);
        },
        /**
         * 清除测量和标记点
         * */
        clearMeasureMark (type) {
            let that = this;
            if (type === 'clear') {
                that.measureVector.getSource().clear();   // 清除测量图层数据
            }
            if (that.measureDraw) {  // 判断是否正在测量，否则关闭测量
                that.map.removeInteraction(this.measureDraw)
                that.measureDraw = null
            }
        },
        styleFunction(feature, segments, drawType, tip) {
            let that = this;
            const styles = [];
            const geometry = feature.getGeometry();
            const type = geometry.getType();
            let point, label, line;
            if (!drawType || drawType === type || type === 'Point') {
                styles.push(measureDefaultStyle);
                if (type === 'Polygon') {
                    point = geometry.getInteriorPoint();
                    label = formatArea(geometry);
                    line = new LineString(geometry.getCoordinates()[0]);
                } else if (type === 'LineString') {
                    point = new Point(geometry.getLastCoordinate());
                    label = formatLength(geometry);
                    line = geometry;
                }
            }
            if (segments && line) {
                let count = 0;
                line.forEachSegment(function (a, b) {
                    const segment = new LineString([a, b]);
                    const label = formatLength(segment);
                    if (segmentStyles.length - 1 < count) {
                        segmentStyles.push(segmentStyle.clone());
                    }
                    const segmentPoint = new Point(segment.getCoordinateAt(0.5));
                    segmentStyles[count].setGeometry(segmentPoint);
                    segmentStyles[count].getText().setText(label);
                    styles.push(segmentStyles[count]);
                    count++;
                });
            }
            if (label) {
                labelStyle.setGeometry(point);
                labelStyle.getText().setText(label);
                styles.push(labelStyle);
            }
            if ( tip && type === 'Point' && !that.measureModify.getOverlay().getSource().getFeatures().length ) {
                that.tipPoint = geometry;
                tipStyle.getText().setText(tip);
                styles.push(tipStyle);
            }
            return styles;
        },
        addMeasure(typeSelect) {
            let that = this;
            that.$emit('closeInteraction')
            const drawType = typeSelect == 'area' ? 'Polygon' : 'LineString';
            const activeTip = '测量' + (drawType === 'Polygon' ? '面积' : '距离');
            const idleTip = '点击开始测量';
            let tip = idleTip;
            that.measureDraw  = new Draw({
                source: that.measureSource,
                type: drawType,
                style: function (feature) {
                return that.styleFunction(feature, true, drawType, tip);
                },
            });
            that.measureDraw.on('drawstart', function () {
                that.measureModify.setActive(false);
                tip = activeTip;
            });
            that.measureDraw.on('drawend', function () {
                modifyMeasureStyle.setGeometry(that.tipPoint);
                that.measureModify.setActive(false);
                that.map.once('pointermove', function () {
                modifyMeasureStyle.setGeometry();
                });
                tip = idleTip;
                that.map.removeInteraction(that.measureDraw);
                that.measureDraw = null;
                that.measureModify.setActive(true);
            });
            that.map.addInteraction(that.measureDraw);
        },
    },
    mounted(){
        let that = this;
        that.$nextTick(() => {
            that.map = window.olMap;
            that.initMeasureLayer()
        })
    }
})
</script>

<style lang="less" scoped>
.map-tool {
  .tool-btn{
    .btn-distance {
        background: url("../../../assets/icons/mapTool/measure-length.png") no-repeat;
        background-size: 100% 100%;
      }
      .btn-area {
        background: url("../../../assets/icons/mapTool/measure-area.png") no-repeat;
        background-size: 90% 90%;
      }
      .btn-clear {
        background: url("../../../assets/icons/mapTool/clear.png") no-repeat;
        background-size: 90% 90%;
      }
  }
}
</style>
