import VectorSource from 'ol/source/Vector'
import VectorLayer from 'ol/layer/Vector'

//行政区划图层
export const regionLayer = new VectorLayer({
  source: new VectorSource({ wrapX: false }),
  zIndex: 11,
});


//测量图层
export const measureLayer = new VectorLayer({
  source: new VectorSource({ wrapX: false }),
  zIndex: 12,
});

//绘制图层
export const drawLayer = new VectorLayer({
  source: new VectorSource({ wrapX: false }),
  zIndex: 12,
});

//导入图层
export const importLayer = new VectorLayer({
  source: new VectorSource({ wrapX: false }),
  zIndex: 11,
});

export const initVectoryLayers = function (map) {
  if (!map) return
  map.addLayer(regionLayer)
  map.addLayer(measureLayer)
  map.addLayer(drawLayer)
  map.addLayer(importLayer)
}
